.excellence {
  background-color: var(--neutral-500);

  &__content {
    background-color: var(--accent-color);
  }

  &__picture {
    grid-column: 8 span;
    display: flex;

    display: block;
    height: 100%;
    width: 100%;
  }

  &__img {
    width: 855px;

    display: block;
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }

  &__text {
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    grid-column: 4 span;
    padding: 30px;

    svg {
      margin: 0 auto;
      margin-bottom: 24px;
    }
  }

  &__title {
    margin: 0;
    color: var(--neutral-100);
    font-size: var(--fs-36);
    font-weight: 400;
    line-height: 44px;
    text-align: center;
    margin-bottom: 32px;
  }

  &__link {
    display: inline-block;
    padding: 16px 40px;
    background-color: var(--neutral-100);
    border-radius: 10em;
    border: 2px solid var(--neutral-100);

    color: var(--neutral-900);
    font-size: var(--fs-18);
    font-weight: 400;
    line-height: 28px;
    text-align: center;
    transition: color var(--trs-35), background-color var(--trs-35);

    @include hover {
      background-color: transparent;
      color: var(--neutral-100);
    }
  }
}
