.shop {
  background-color: var(--neutral-500);
  padding: 30px 0 70px 0;

  &__title {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-48);
    font-weight: 400;
    line-height: 54px;
    text-align: center;
    margin-bottom: 43px;
  }

  &__content {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 22px;
  }
}

.card {
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  picture {
    display: flex;
  }

  &--one {
    border: 2px solid hsla(19, 81%, 54%, 1);
    box-shadow: 8px 8px 0px 0px hsla(247, 68%, 87%, 1);

  }

  &--two {
    border: 2px solid hsla(219, 71%, 46%, 1);
    box-shadow: 8px 8px 0px 0px hsla(50, 77%, 58%, 1);
  }

  &--three {
    border: 2px solid hsla(231, 33%, 21%, 1);
    box-shadow: 8px 8px 0px 0px hsla(162, 37%, 73%, 1);
  }

  &--four {
    border: 2px solid hsla(6, 72%, 54%, 1);
    box-shadow: 8px 8px 0px 0px hsla(17, 69%, 41%, 1);
  }

  &__text {
    padding: 15px 0;
    color: var(--neutral-900);
    font-size: var(--fs-17);
    font-weight: 400;
    line-height: 28px;
    text-align: center;
    transition: color var(--trs-35);

    @include hover {
      color: var(--accent-color);
    }
  }
}
