.hero {
  background-color: var(--neutral-500);
  padding: 80px 0;

  &__content {
    gap: 36px;
  }

  &__text {
    grid-column: 6 span;
    align-self: center;
    max-width: 523px;
    padding-left: 15px;
    margin-left: auto;
  }

  &__title {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-64);
    font-weight: 400;
    line-height: 76px;
    margin-bottom: 18px;
  }

  &__desc {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-18);
    font-weight: 400;
    line-height: 28px;
    margin-bottom: 46px;
  }

  &__link {
    display: inline-block;
    color: var(--neutral-900);
    font-size: var(--fs-18);
    font-weight: 400;
    line-height: 28px;

    padding: 16px 32px;
    background-color: var(--accent-color);
    border-radius: 10em;
    border: 2px solid var(--accent-color);
    transition: background-color var(--trs-35);

    @include hover {
      background-color: transparent;
    }
  }

  &__picture {
    grid-column: 6 span;
    display: block;
    height: 100%;
    width: 100%;
  }

  &__img {
    display: block;
    height: 100%;
    object-fit: cover;
    object-position: 0 center;
  }
}
