.apartment {
  padding: 39px 0 42px 0;
  background-color: hsla(7, 71%, 88%, 1);

  &__text {
    display: block;
    margin-inline: auto;
    color: var(--neutral-900);
    font-size: var(--fs-88);
    font-weight: 400;
    line-height: 100px;
    text-align: center;
    max-width: 933px;
    margin-bottom: 50px;
  }

  &__brands {
    display: flex;
    align-items: center;
    gap: 20px;
    justify-content: space-between;
    padding: 0 15px;
  }
}
