.furniture {
  padding: 130px 0 90px 0;
  background-color: var(--neutral-100);

  &__content {
    display: flex;
    text-align: center;
    flex-direction: column;
    max-width: 892px;
    margin-inline: auto;
    padding: 0 15px;
  }

  &__title {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-36);
    font-weight: 400;
    line-height: 44px;
    margin-bottom: 16px;
  }

  &__desc {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-24);
    font-weight: 400;
    line-height: 32px;
    text-align: center;
  }
}
