.bestsellers {
  background-color: var(--neutral-500);
  padding: 112px 0 144px 0;

  &__title {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-36);
    font-weight: 400;
    line-height: 44px;
    text-align: center;
    margin-bottom: 57px;
  }

  &__content {
    gap: 5px;
    max-width: 944px;
    margin: 0 auto;
    margin-bottom: 55px;
  }

  &__wrap {
    display: block;
    text-align: center;
  }

  &__link {
    position: relative;
    display: inline-block;
    color: var(--accent-color);
    font-size: var(--fs-18);
    font-weight: 400;
    line-height: 28px;
    text-align: center;

    &:after {
      content: '';
      width: 100%;
      height: 2px;
      background-color: var(--accent-color);
      display: inline-block;
      position: absolute;
      bottom: -4px;
      left: 0;
      transform: translateY(5px);
      opacity: 0;
      transition: transform var(--trs-35), opacity var(--trs-35);
    }

    &:hover:after {
      content: '';
      width: 100%;
      height: 2px;
      background-color: var(--accent-color);
      display: inline-block;
      position: absolute;
      bottom: -4px;
      left: 0;
      transform: translateY(0px);
      opacity: 1;
    }
  }
}

.product {
  display: flex;
  flex-direction: column;
  height: 100%;

  padding: 40px 32px;
  text-align: center;
  grid-column: 3 span;
  background-color: var(--neutral-100);

  &__content {
    margin-top: auto;
  }

  &__img {
    margin-inline: auto;
    display: block;
    margin-bottom: 30px;
  }

  &__title {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-18);
    font-weight: 500;
    line-height: 28px;
    margin-bottom: 10px;
  }

  &__desc {
    margin: 0;
    margin-inline: auto;
    color: var(--neutral-900);
    font-size: var(--fs-18);
    font-weight: 400;
    line-height: 28px;
    max-width: 150px;
    text-align: center;

    &--color {
      color: var(--accent-color);
    }
  }
}
