.footer {
  background-color: hsl(34, 23%, 94%);
  padding: 80px 0;
  border-top: 2px solid hsla(63, 60%, 58%, 1);

  &__list {
    padding: 0;
    margin: 0;
    list-style: none;
    padding: 0 15px;
    gap: 40px;
  }

  &__item {
    width: 100%;
    max-width: 228px;

    &--four {
      width: 100%;
      max-width: 600px;
    }
  }

  &__link {
    display: block;
    color: var(--neutral-900);
    font-size: var(--fs-16);
    font-weight: 400;
    line-height: 34px;
    text-align: left;
    transition: color var(--trs-35);

    @include hover {
      color: var(--accent-color);
    }
  }

  &__title {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-16);
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 1.27px;
    text-transform: uppercase;
    text-align: left;
    margin-bottom: 24px;
  }

  &__subtitle {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-16);
    font-weight: 600;
    line-height: 24px;
    text-transform: uppercase;
    letter-spacing: 1.27px;
    text-align: left;

  }

  &__subscription {}
}

.social {
  display: flex;
  gap: 15px;
  margin-bottom: 54px;
}

.social-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  border-radius: 10em;
  border: 2px solid var(--neutral-900);
  fill: var(--neutral-900);
  transition: border var(--trs-35), fill var(--trs-35);

  @include hover {
    border: 2px solid var(--accent-color);
    fill: var(--accent-color);
  }
}

.icon-footer {}

.icon {}


.newsletter {
  display: flex;
  align-items: flex-end;
  width: 100%;
  max-width: 298px;
  border-bottom: 2px solid var(--neutral-900);
  background-color: transparent;
  height: 60px;

  &__label {
    width: 100%;
  }

  &__input {
    background-color: transparent;
    width: 100%;
    height: 40px;
    outline: none;
  }
}

.window-icon {
  display: block;
  margin-left: auto;
  margin-right: 15px;
}
