.content {
  padding: 50px 0 180px 0;

  &__list {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 60px;
  }

  &__item {
    gap: 36px;
  }

  &__text {
    grid-column: 6 span;
    align-self: center;
    max-width: 442px;
    margin-inline: auto;
    padding-left: 15px;
  }

  &__title {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-36);
    font-weight: 400;
    line-height: 44px;
    text-align: left;
    margin-bottom: 20px;
  }

  &__desc {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-18);
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0em;
    text-align: left;
    margin-bottom: 33px;

    a {
      color: var(--neutral-900);
      transition: color var(--trs-35);

      @include hover {
        color: var(--accent-color);
      }
    }
  }

  &__link {
    position: relative;
    color: var(--accent-color);
    font-size: var(--fs-18);
    font-weight: 400;
    line-height: 28px;
    text-align: left;

    &:after {
      content: '';
      width: 100%;
      height: 2px;
      background-color: var(--accent-color);
      display: inline-block;
      position: absolute;
      bottom: -4px;
      left: 0;
      transform: translateY(5px);
      opacity: 0;
      transition: transform var(--trs-35), opacity var(--trs-35);
    }

    &:hover:after {
      content: '';
      width: 100%;
      height: 2px;
      background-color: var(--accent-color);
      display: inline-block;
      position: absolute;
      bottom: -4px;
      left: 0;
      transform: translateY(0px);
      opacity: 1;
    }
  }

  &__picture {
    grid-column: 6 span;
    display: block;
    height: 418px;
    width: 100%;
    z-index: 2;

    &--height {
      height: 578px;
    }
  }

  &__img {
    display: block;
    height: 100%;
    object-fit: cover;
    object-position: 0 center;
    margin-inline: auto;

    &--border {
      border: 15px solid hsl(63, 60%, 58%);
    }
  }
}
