.reviews {
  padding: 143px 0 176px 0;
  background-color: var(--neutral-500);

  &__title {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-48);
    font-weight: 400;
    line-height: 54px;
    text-align: center;
    margin-bottom: 57px;
  }
}

.reviewsSlider {
  max-width: 1253px;
  padding: 10px 10px;
  position: relative;
  left: 80px;
}

.swiperSlide {
  display: flex;
  flex-direction: column;
  height: auto;
}

.comment {
  padding: 34px;
  display: flex;
  flex-direction: column;
  height: 100%;

  svg {
    margin-bottom: 26px;
  }

  &--one {
    border: 2px solid hsla(19, 81%, 54%, 1);
    box-shadow: 8px -8px 0px 0px hsla(247, 68%, 87%, 1);
    fill: hsla(247, 68%, 87%, 1);
  }

  &--two {
    border: 2px solid hsla(219, 71%, 46%, 1);
    box-shadow: 8px -8px 0px 0px hsla(50, 77%, 58%, 1);
    fill: hsla(50, 77%, 58%, 1);
  }

  &--three {
    border: 2px solid hsla(231, 33%, 21%, 1);
    box-shadow: 8px -8px 0px 0px hsla(162, 37%, 73%, 1);
    fill: hsla(162, 37%, 73%, 1);
  }

  &__desc {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-18);
    font-weight: 400;
    line-height: 28px;
    text-align: left;
    margin-bottom: 30px;
  }

  &__text {
    color: var(--neutral-900);
    font-size: var(--fs-16);
    font-weight: 700;
    line-height: 24px;
    text-align: left;
    margin-top: auto;
  }
}
