.header {
  width: 100%;
  position: sticky;
  top: 0;
  background-color: var(--neutral-500);
  z-index: 1000;

  &__content {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
  }

  &__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.left {

  &__list {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    align-items: center;
  }

  &__item:not(:last-child) {
    margin-right: 36px;
  }

  &__link {
    color: var(--neutral-900);
    font-size: var(--fs-16);
    font-weight: 400;
    line-height: 20px;
    transition: color var(--trs-35);

    @include hover {
      color: var(--accent-color);
    }
  }
}

.right {

  &__list {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    align-items: center;
  }

  &__item:not(:last-child) {
    margin-right: 36px;
  }

  &__link {
    color: var(--neutral-900);
    font-size: var(--fs-16);
    font-weight: 400;
    line-height: 20px;
    transition: color var(--trs-35);

    @include hover {
      color: var(--accent-color);
    }
  }

  &__cart {
    fill: var(--neutral-900);
    transition: fill var(--trs-35);

    @include hover {
      fill: var(--accent-color);
    }
  }
}
