:root {
  --ff-primary: "Roboto", sans-serif;

  --content-width: 73.75rem; // 1180px;
  --container-offset: 0.9375rem; // 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  /* ------------------- */
  /* color theme         */
  /* ------------------- */
  --neutral-100: hsl(0, 0%, 100%);
  --neutral-500: hsl(30, 43%, 97%);
  --neutral-900: hsl(0, 0%, 0%);

  --accent-color: hsl(19, 81%, 54%);
  /* ------------------- */
  /* font-size           */
  /* ------------------- */
  --fs-16: 1rem; // 16px;
  --fs-17: 1.0625rem; // 17px;
  --fs-18: 1.125rem; // 18px;
  --fs-24: 1.5rem; // 24px;
  --fs-36: 2.25rem; // 36px;
  --fs-48: 3rem; // 48px;
  --fs-64: 4rem; // 64px;
  --fs-88: 5.5rem; // 88px;
  /* ------------------- */
  /* transition          */
  /* ------------------- */
  --trs-35: .35s ease-in-out;
  /* ------------------- */
  /* margin              */
  /* ------------------- */
  --mrg-50: 3.125rem; // 50px;
  --mrg-100: 6.25rem; // 100px;
  --mrg-150: 9.375rem; // 150px;
  --mrg-200: 12.5rem; // 200px;
  /* ------------------- */
  /* gap                 */
  /* ------------------- */
  --gap10: 0.625rem; // 10px;
  --gap20: 1.25rem; // 20px;
}









