.stress-free {
  padding: 120px 0;
  background-color: var(--neutral-500);

  &__icon {
    display: block;
    margin-inline: auto;
    margin-bottom: 41px;
  }

  &__desc {
    margin: 0;
    color: var(--neutral-900);
    font-size: var(--fs-24);
    font-weight: 400;
    line-height: 32px;
    text-align: center;
    max-width: 292px;
    margin-inline: auto;
    margin-bottom: 39px;
  }

  &__link {
    display: inline-block;
    padding: 16px 40px;
    background-color: var(--accent-color);

    color: var(--neutral-900);
    font-size: var(--fs-18);
    font-weight: 400;
    line-height: 28px;
    border-radius: 10em;
    border: 2px solid var(--accent-color);
    transition: background-color var(--trs-35);

    @include hover {
      background-color: transparent;
    }
  }
}

.stress-free-wrap {
  display: block;
  text-align: center;
}
